import React, { useState } from 'react'
const IMAGE_API = "https://image.tmdb.org/t/p/w500/"

const Movies = ({title,poster_path,overview,vote_average}) => {
    const[show,setShow] = useState(false)
    console.log(show)
    return (
        <div className="moviesComponent" onClick={()=>setShow(!show)}>
            <div className="moviesComponent__header">
                <img src={IMAGE_API+poster_path} alt={title} />
            </div>
            <div className="moviesComponent__info">
                <h3>{title}</h3>
                <h3>{vote_average}</h3>
                <div className={`${show? "moviesComponent__overview":"moviesComponent__overviewDisable"}`}>
                   <h4>{overview}</h4>
            </div>
            </div>

            
        </div>
    )
}

export default Movies
